.todo {
  display: block;
  width: 100%;
  text-align: left;
  clear: left;
  height: 40px;
  line-height: 35px;
}
.todo-checkbox {
  width: 10%;
  text-align: center;
}

.todo div {
  float: left;
}
.todo-name {
  width: 80%;
}
.todo-striped {
  background-color: rgb(241, 240, 240);
}
.del-todo {
  text-align: center;
  font-size: 20pt;
  width: 10%;
  cursor: pointer;
}
.todo-completed {
  color: grey;
  text-decoration: line-through;
}
