.dash-box {
  margin: 20px;
  min-height: 200px;
  min-width: 300px;
  overflow: hidden;
  box-shadow: 2px 5px 10px rgb(23, 26, 29);
}
.dash-box-header {
  height: 50px;
  width: 100%;
}
.dash-box-title {
  padding: 12px 0px 0px 10px;
  float: left;
  font-size: 16pt;
}
.dash-box-header-input {
  line-height: 20pt;
  margin-top: 8.5px;
  width: 60%;
}
.dash-box-header-btn {
  margin: 9px 10px 0px 0px;
  /* padding-right: 23px; */
  /* padding-bottom: 5px; */
  float: right;
  width: 30px;
  height: 30px;
  border-radius: 20px;
  font-size: 20pt;
  line-height: 20pt;
  cursor: pointer;
}

.dash-box-content {
  width: 100%;
  margin: auto;
  overflow: scroll;
  max-height: 150px;
}

.flex-proportion-1 {
  flex-grow: 1;
  flex-shrink: 1;
}
.flex-proportion-2 {
  flex-grow: 2;
  flex-shrink: 2;
}
.flex-proportion-3 {
  flex-grow: 3;
  flex-shrink: 3;
}
