body {
  padding-top: 105px;
  margin: 0;
  font-family: Verdana, Geneva, Tahoma, sans-serif;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar {
  width: 100%;
  margin: 0px 0px 40px 0px;
  z-index: 999;
  min-height: 50px;
  padding: 10px;
  position: fixed;
  top: 0px;
  left: 0px;
}

/*Color Schemes*/
.grn-wht {
  background-color: rgb(118, 185, 38);
  color: white;
}
.drkgr-wht {
  background-color: rgb(23, 26, 29);
  color: white;
}
.wht-drkgr {
  background-color: white;
  color: rgb(23, 26, 29);
}
.gr-wht {
  background-color: rgb(54, 58, 61);
  color: white;
}

.btn {
  margin-bottom: 20px;
  font-size: 12pt;
  border: none;
  padding: 10px 15px 10px 15px;
}

.mw-100 {
  max-width: 100px;
}
.mw-200 {
  max-width: 200px;
}
.mw-300 {
  max-width: 300px;
}
.mw-400 {
  max-width: 400px;
}

.mw-500 {
  max-width: 500px;
}
